import conn from './connection'

export default {
    login(user) {
        return conn.$axios({
            url: '/auth/login',
            method: 'POST',
            data: user
        })
    },
    register(data) {
        return conn.$axios({
            url: '/auth/register',
            method: 'POST',
            data: data
        })
    },
    validate(params) {
        return conn.$axios({
            url: '/auth/validate',
            method: 'GET',
            params: params
        })
    },
    forget(params) {
        return conn.$axios({
            url: '/auth/forget',
            method: 'GET',
            params: params
        })
    },
    restart(data) {
        return conn.$axios({
            url: '/auth/restart',
            method: 'POST',
            data: data
        })
    },
    getProfile() {
        return conn.$axios({
            url: '/user',
            method: 'GET'
        })
    }
}