import i18n from '../lang/'

export default {
    namespaced: true,
    state: {
        loadedLanguages: ['en']
    },
    mutations: {
        ['ADD_LOCALE']: (state, lang) => {
            state.loadedLanguages.push(lang)
        }
    },
    actions: {
        ['SET_LANG']: ({ commit, state }, payload) => {
            const lang = payload.lang
            if (i18n.locale !== lang) {
              if (!state.loadedLanguages.includes(lang)) { // lazy load language
                return import(`@/lang/${lang}`)
                .then(msgs => {
                  i18n.setLocaleMessage(lang, msgs.default)
                  commit('ADD_LOCALE', lang)
      
                  i18n.locale = lang
                  document.querySelector('html').setAttribute('lang', payload)
                })
              }
              i18n.locale = lang
              document.querySelector('html').setAttribute('lang', payload)
            }
        }
    }
}