import conn from './connection'

export default {
    // Exercise Module
    getExerciseDashboard() {
        return conn.$axios({
            url: '/exercise/dashboard',
            method: 'GET'
        })
    },
    getExerciseResources(exerciseId) {
        return conn.$axios({
            url: `/exercise/${exerciseId}/resource`,
            method: 'GET',
            responseType: 'blob'
        })
    },
    getExerciseInstructions(exerciseId) {
        return conn.$axios({
            url: `/exercise/${exerciseId}/instructions`,
            method: 'GET',
            responseType: 'blob'
        })
    },
    getExerciseTemplate(exerciseId) {
        return conn.$axios({
            url: `/exercise/${exerciseId}/template`,
            method: 'GET',
            responseType: 'blob'
        })
    },
    getUserSolution(exerciseId, solutionId) {
        return conn.$axios({
            url: `/exercise/${exerciseId}/solutions/${solutionId}/project`,
            method: 'GET',
            responseType: 'blob'
        })
    },
    getExerciseUserSolutions(exerciseId) {
        return conn.$axios({
            url: `/exercise/${exerciseId}/solutions`,
            method: 'GET'
        })
    },
    /*
    Expected params format
    {
        query: string,
        pageNumber: int,
        pageSize: int,
        sortField: string,
        sortDirection: string
    }
    */
    getExerciseListByQuery(params) {
        return conn.$axios({
            url: `/exercise/search?q=${params.query}&page=${params.page}&size=${params.size}&sort=${params.sortField},${params.sortDirection}`,
            method: 'GET'
        })
    },
    uploadAssessedSolution(exerciseId, solution) {
        const formData = new FormData();
        formData.append("solution", solution.file);
        formData.append("mark", solution.mark);
        return conn.$axios({
            url: `/exercise/${exerciseId}/solution`,
            method: 'POST',
            data: formData,
        })
    },
    calculateMarkFromAssess(assessResponse) {
        const properties = assessResponse.substring(1, assessResponse.length - 1).split(";");
        // const assessmentMode = properties.shift();
        const regex = /\d+\.\d+/g;
        var solutionMark = 0.0;

        properties.forEach(property => {
            const marks = property.match(regex);

            if (marks != null) {
                solutionMark += parseFloat(marks[0], 10);
            }
        });

        return solutionMark;
    },
}