<style scoped lang="scss">
$bdrds: 3px;
$white: #fff;
$black: #000;
$gray: #ccc;
$salmon: #e8716d;
$smoky-black: rgba(#222222, 0.85);
$ff: "Montserrat", sans-serif;
$ff-light: 300;

/**
  * General configs
  */
:root,
.md-button.md-theme-default.md-primary,
.md-field.md-theme-default:before,
.md-field.md-theme-default.md-focused label {
  --md-theme-default-primary: #e8716d;
  --md-theme-default-primary-on-background: #e8716d;
}
button {
  background-color: transparent;
  padding: 0;
  border: 0;
  outline: 0;
  cursor: pointer;
}
input {
  background-color: transparent;
  padding: 0;
  border: 0;
  outline: 0;

  &[type="submit"] {
    cursor: pointer;
  }
  &::placeholder {
    font-size: 0.85rem;
    font-family: $ff;
    font-weight: $ff-light;
    letter-spacing: 0.1rem;
    color: $gray;
  }
}
/**
  * Page background
  */
.user_options-container {
  position: relative;
  //width: 80%;
}
.user_options-text {
  display: flex;
  justify-content: space-between;
  width: 100%;
  background-color: $smoky-black;
  border-radius: $bdrds;
}

/**
  * Registered and Unregistered user box and text
  */
.user_options-registered,
.user_options-unregistered {
  width: 50%;
  padding: 75px 45px;

  color: $white;
  font-weight: $ff-light;
}
.user_registered-title,
.user_unregistered-title {
  margin-bottom: 15px;
  font-size: 1.66rem;
  line-height: 1em;
}
.user_unregistered-text,
.user_registered-text {
  font-size: 0.83rem;
  line-height: 1.4em;
}
.user_registered-login,
.user_unregistered-signup {
  margin-top: 30px;
  border: 1px solid $gray;
  border-radius: $bdrds;
  padding: 10px 30px;

  color: $white;
  text-transform: uppercase;
  line-height: 1em;
  letter-spacing: 0.2rem;

  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;

  &:hover {
    color: $smoky-black;
    background-color: $gray;
  }
}

/**
  * Login and signup forms
  */
.user_options-forms {
  position: absolute;
  top: 50%;
  left: 30px;

  width: calc(50% - 30px);
  min-height: 420px;
  background-color: $white;
  border-radius: $bdrds;
  box-shadow: 2px 0 15px rgba(#000, 0.25);
  overflow: hidden;

  transform: translate3d(100%, -50%, 0);
  transition: transform 0.4s ease-in-out;

  .forms {
    &_title {
      margin-bottom: 45px;

      font-size: 1.5rem;
      font-weight: 500;
      line-height: 1em;
      text-transform: uppercase;
      color: $salmon;
      letter-spacing: 0.1rem;
    }
    &_field {
      &:not(:last-of-type) {
        margin-bottom: 20px;
      }
    }
    &_field-input {
      width: 100%;
      border-bottom: 1px solid $gray;
      padding: 6px 20px 6px 0;

      font-family: $ff;
      font-size: 1rem;
      font-weight: $ff-light;
      color: darken($gray, 30%) !important;
      letter-spacing: 0.1rem;

      transition: border-color 0.2s ease-in-out;

      &:focus {
        border-color: darken($gray, 30%);
      }
    }
    &_buttons {
      display: flex;
      justify-content: space-between;
      align-items: center;

      margin-top: 35px;

      &-forgot {
        font-family: $ff;
        letter-spacing: 0.1rem;
        color: $gray;
        text-decoration: underline;

        transition: color 0.2s ease-in-out;

        &:hover {
          color: darken($gray, 10%);
        }
      }

      &-action {
        background-color: $salmon;
        border-radius: $bdrds;
        padding: 10px 35px;

        font-size: 1rem;
        font-family: $ff;
        font-weight: $ff-light;
        color: $white;
        text-transform: uppercase;
        letter-spacing: 0.1rem;

        transition: background-color 0.2s ease-in-out;

        &:hover {
          background-color: darken($salmon, 10%);
        }
      }
    }
  }

  .user_forms-signup,
  .user_forms-login {
    position: absolute;
    top: 70px;
    left: 40px;
    overflow-x: hidden;
    width: calc(100% - 80px);

    opacity: 0;
    visibility: hidden;
    transition: opacity 0.4s ease-in-out, visibility 0.4s ease-in-out,
      transform 0.5s ease-in-out;
  }
  .user_forms-signup {
    transform: translate3d(120px, 0, 0);
    top: 30px;
    .forms_title {
      margin-bottom: 0px;
    }
    .md-field {
      margin: 15px 0px;
    }
    .forms_buttons {
      justify-content: flex-end;
    }
  }
  .user_forms-login {
    transform: translate3d(0, 0, 0);
    opacity: 1;
    visibility: visible;
  }
}

/**
  * Triggers
  */
.user_options-forms {
  &.bounceLeft {
    animation: bounceLeft 1s forwards;

    .user_forms-signup {
      animation: showSignUp 1s forwards;
    }
    .user_forms-login {
      opacity: 0;
      visibility: hidden;
      transform: translate3d(-120px, 0, 0);
    }
  }
  &.bounceRight {
    animation: bounceRight 1s forwards;
  }
}
/**
  * Responsive 990px
  */
@media screen and (max-width: 990px) {
  .user_options-container {
    width: 100%;

    .user_options-forms {
      .forms_buttons {
        flex-direction: column;
      }
      .user_forms-login {
        .forms_buttons-action {
          margin-top: 20px;
        }
      }
      .user_forms-signup {
        top: 40px;
        .forms_title {
          margin-bottom: 10px;
        }
      }
    }
    .user_options-registered,
    .user_options-unregistered {
      padding: 50px 45px;
    }
  }
}
/**
  * Bounce to the left side
  */
@keyframes bounceLeft {
  0% {
    transform: translate3d(100%, -50%, 0);
  }
  50% {
    transform: translate3d(-30px, -50%, 0);
  }
  100% {
    transform: translate3d(0, -50%, 0);
  }
}

/**
  * Bounce to the left side
  */
@keyframes bounceRight {
  0% {
    transform: translate3d(0, -50%, 0);
  }
  50% {
    transform: translate3d(calc(100% + 30px), -50%, 0);
  }
  100% {
    transform: translate3d(100%, -50%, 0);
  }
}

/**
  * Show Sign Up form
  */
@keyframes showSignUp {
  100% {
    opacity: 1;
    visibility: visible;
    transform: translate3d(0, 0, 0);
  }
}

@media only screen and (max-width: 600px) {
  .user_forms-login {
    left: 20px;
    width: calc(100% - 40px);
  }
  .user_options-forms {
    left: 0px;
    width: 50%;
  }
  .user_registered-text,
  .user_unregistered-text {
    display: none;
  }
}
</style>

<template>
  <div class="login-register-form">
    <div class="user_options-container">
      <div class="user_options-text">
        <div class="user_options-unregistered">
          <h2 class="user_unregistered-title">{{ $t("index.user_unregistered_title") }}</h2>
          <!-- Don't have an account? -->
          <p class="user_unregistered-text">{{ $t("index.user_unregistered_text") }}</p>
          <button
            @click="changeForm"
            class="user_unregistered-signup"
            id="signup-button"
          >{{ $t("index.user_unregistered_signup") }}</button>
        </div>

        <div class="user_options-registered">
          <h2 class="user_registered-title">{{ $t("index.user_registered_title") }}</h2>
          <p class="user_registered-text">{{ $t("index.user_registered_text") }}</p>
          <button
            @click="changeForm"
            class="user_registered-login"
            id="login-button"
          >{{ $t("index.user_registered_login") }}</button>
        </div>
      </div>

      <div
        :class="{ bounceLeft: bounceForm, bounceRight: (bounceForm==false)}"
        class="user_options-forms"
        id="user_options-forms"
      >
        <div class="user_forms-login">
          <h2 class="forms_title">{{ $t("index.forms_title_login") }}</h2>

          <form
            v-show="remember"
            @submit.prevent="validateForm('loginForm', login)"
            class="forms_form"
          >
            <fieldset class="forms_fieldset">
              <md-field :class="getValidationClass('loginForm','username')">
                <label for="login-username">{{ $t("index.form_label_username") }}</label>
                <md-input
                  v-model="loginForm.username"
                  :disabled="ifLoading"
                  type="text"
                  name="username"
                  id="login-username"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.loginForm.username.required"
                >{{ $t("index.form_username_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.loginForm.username.minlength"
                >{{ $t("index.form_username_invalid") }}</span>
              </md-field>

              <md-field :class="getValidationClass('loginForm','password')">
                <label for="login-password">{{ $t("index.form_label_password") }}</label>
                <md-input
                  v-model="loginForm.password"
                  :disabled="ifLoading"
                  type="password"
                  name="password"
                  id="login-password"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.loginForm.password.required"
                >{{ $t("index.form_password_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.loginForm.password.minlength"
                >{{ $t("index.form_password_invalid") }}</span>
              </md-field>
            </fieldset>
            <div class="forms_buttons">
              <button
                @click.prevent="changeRemmember"
                type="button"
                class="forms_buttons-forgot"
              >{{ $t("index.form_forgot_button") }}</button>
              <input
                type="submit"
                :value="$t('index.form_login_button')"
                class="forms_buttons-action"
              >
            </div>
          </form>

          <form
            v-show="!remember"
            @submit.prevent="validateForm('forgetForm', forgetPassword)"
            class="forms_form"
          >
            <fieldset class="forms_fieldset">
              <md-field :class="getValidationClass('forgetForm','email')">
                <label for="forget-email">{{ $t("index.form_label_email") }}</label>
                <md-input
                  v-model="forgetForm.email"
                  :disabled="ifLoading"
                  type="text"
                  name="email"
                  id="forget-email"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.forgetForm.email.required"
                >{{ $t("index.form_email_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.forgetForm.email.minlength"
                >{{ $t("index.form_email_invalid") }}</span>
              </md-field>
            </fieldset>

            <div class="forms_buttons">
              <button
                @click.prevent="changeRemmember"
                type="button"
                class="forms_buttons-forgot"
              >{{ $t("index.form_remember_button") }}</button>
              <input
                type="submit"
                :value="$t('index.form_resetpass_button')"
                class="forms_buttons-action"
              >
            </div>
          </form>
        </div>

        <div class="user_forms-signup">
          <h2 class="forms_title">{{ $t("index.forms_title_register") }}</h2>

          <form @submit.prevent="validateForm('registerForm', register)" class="forms_form">
            <fieldset class="forms_fieldset">
              <md-field :class="getValidationClass('registerForm','username')">
                <label for="register-username">{{ $t("index.form_label_username") }}</label>
                <md-input
                  v-model="registerForm.username"
                  :disabled="ifLoading"
                  type="text"
                  name="username"
                  id="register-username"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.registerForm.username.required"
                >{{ $t("index.form_username_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.registerForm.username.minlength"
                >{{ $t("index.form_username_invalid") }}</span>
              </md-field>

              <md-field :class="getValidationClass('registerForm','email')">
                <label for="register-email">{{ $t("index.form_label_email") }}</label>
                <md-input
                  v-model="registerForm.email"
                  :disabled="ifLoading"
                  type="text"
                  name="email"
                  id="register-email"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.registerForm.email.required"
                >{{ $t("index.form_email_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.registerForm.email.minlength"
                >{{ $t("index.form_email_invalid") }}</span>
              </md-field>

              <md-field :class="getValidationClass('registerForm','password')">
                <label for="register-password">{{ $t("index.form_label_password") }}</label>
                <md-input
                  v-model="registerForm.password"
                  :disabled="ifLoading"
                  type="password"
                  name="password"
                  id="register-password"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.registerForm.password.required"
                >{{ $t("index.form_password_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.registerForm.password.minlength"
                >{{ $t("index.form_password_invalid") }}</span>
              </md-field>

              <md-field :class="getValidationClass('registerForm','passwordRepeat')">
                <label for="register-passwordRepeat">{{ $t("index.form_label_repeatpass") }}</label>
                <md-input
                  v-model="registerForm.passwordRepeat"
                  :disabled="ifLoading"
                  type="password"
                  name="passwordRepeat"
                  id="register-passwordRepeat"
                  autocomplete="given-name"
                />
                <span
                  class="md-error"
                  v-if="!$v.registerForm.passwordRepeat.required"
                >{{ $t("index.form_repeatpass_required") }}</span>
                <span
                  class="md-error"
                  v-else-if="!$v.registerForm.passwordRepeat.minlength"
                >{{ $t("index.form_repeatpass_invalid") }}</span>
              </md-field>
            </fieldset>
            <div class="forms_buttons">
              <input
                type="submit"
                :value="$t('index.form_register_button')"
                class="forms_buttons-action"
              >
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { validationMixin } from "vuelidate";
import { required, minLength, sameAs, email } from "vuelidate/lib/validators";

export default {
  name: "login-register-form",
  mixins: [validationMixin],
  data() {
    return {
      loginForm: {
        username: null,
        password: null
      },
      forgetForm: {
        email: null
      },
      registerForm: {
        username: null,
        password: null,
        passwordRepeat: null,
        email: null
      },
      bounceForm: null,
      remember: true
    };
  },
  validations: {
    loginForm: {
      username: {
        required,
        minLength: minLength(4)
      },
      password: {
        required,
        minLength: minLength(4)
      }
    },
    forgetForm: {
      email: {
        required,
        email,
        minLength: minLength(4)
      }
    },
    registerForm: {
      username: {
        required,
        minLength: minLength(4)
      },
      password: {
        required,
        minLength: minLength(4)
      },
      passwordRepeat: {
        required,
        minLength: minLength(4),
        sameAsPassword: sameAs("password")
      },
      email: {
        required,
        email,
        minLength: minLength(4)
      }
    }
  },
  computed: {
    ifLoading: function() {
      return this.$store.state.auth.status == "loading";
    }
  },
  methods: {
    // Main actions
    login: function() {
      const { username, password } = this.loginForm;
      this.$store
        .dispatch("auth/AUTH_LOGIN", { username, password })
        .then(() => {
          this.$router.push("/intranet/dashboard");
        });
    },
    register: function() {
      if (this.validateRegisterForm()) {
        const { username, password, email } = this.registerForm;
        this.$store
          .dispatch("auth/AUTH_REGISTER", { username, password, email })
          .then(() => {
            this.clearForm(this.registerForm);
            alert("See your inbox!");
            //this.$router.push("/intranet");
          });
      }
    },
    forgetPassword: function() {
      const { email } = this.forgetForm;
      this.$store.dispatch("auth/AUTH_FORGET", { email }).then(() => {
        this.clearForm(this.forgetForm);
        alert("See your inbox!");
        //this.$router.push("/intranet");
      });
    },
    // Change Forms
    changeForm: function() {
      this.bounceForm != null
        ? (this.bounceForm = !this.bounceForm)
        : (this.bounceForm = true);
    },
    changeRemmember: function() {
      this.remember = !this.remember;
    },
    validateRegisterForm: function() {
      if (this.password != this.passwordRepeat) {
        return false;
      }
      return true;
    },
    // New Actions
    getValidationClass(form, fieldName) {
      const field = this.$v[form][fieldName];
      if (field) {
        return {
          "md-invalid": field.$invalid && field.$dirty
        };
      }
    },
    validateForm(form, callback) {
      this.$v.$touch();
      if (!this.$v[form].$invalid) {
        callback();
      }
    },
    clearForm(form) {
      for (var key in form) {
        form[key] = "";
      }
    }
  }
};
</script>
