import Vue from 'vue'
import VueI18n from 'vue-i18n'

import en from './en' ; // preload default language

Vue.use(VueI18n)

const messages = {
  en: en // default language
}

const i18n = new VueI18n({
  locale: 'en', // actual language
  messages, // set locale messages
})

export default i18n