import service from '@/services/'

export default {
    namespaced: true,
    state: {
        status: '',
        error: null,
        dashboard: null,
        exploreList: null,
        exerciseSolutions: [],
    },
    mutations: {
        ['EXERCISE_REQUEST']: (state) => {
            state.status = 'loading'
        },
        ['EXERCISE_SUCCESS']: (state) => {
            state.status = 'success'
        },
        ['EXERCISE_ERROR']: (state, error) => {
            state.status = 'error',
                state.status = error
        },
        ['SET_DASHBOARD']: (state, data) => {
            state.dashboard = data
        },
        ['SET_PAGE_LIST']: (state, data) => {
            state.exploreList.push(data)
        },
        ['SET_NEW_SEARCH']: (state, data) => {
            if (state.exploreList == null || data.first) {
                state.exploreList = data
            } else {
                data.content = [...state.exploreList.content, ...data.content]
                state.exploreList = data
            }
        },
        ['SET_EXERCISE_SOLUTIONS']: (state, data) => {
            state.exerciseSolutions = data;
        },
        ['ADD_SOLUTION']: (state, data) => {
            state.exerciseSolutions.push(data);
        }
    },
    actions: {
        ['DASHBOARD']: ({ commit }) => {
            commit('EXERCISE_REQUEST')
            service.exercise.getExerciseDashboard()
                .then(resp => {
                    commit('SET_DASHBOARD', resp.data)
                    commit('EXERCISE_SUCCESS')
                })
                .catch(() => {
                    commit('EXERCISE_ERROR')
                })
        },
        ['EXPLORE_LIST']: ({ commit }, pagination) => {
            let params = {
                query: pagination.query || '',
                page: pagination.page || 0,
                size: pagination.size || 12,
                sortField: pagination.sortField || 'creationDate',
                sortDirection: pagination.sortDirection || 'DESC'
            }
            commit('EXERCISE_REQUEST')
            service.exercise.getExerciseListByQuery(params)
                .then(resp => {
                    commit('SET_NEW_SEARCH', resp.data)
                    commit('EXERCISE_SUCCESS')
                })
                .catch((err) => {
                    commit('EXERCISE_ERROR', err)
                })
        },
        ['GET_EXERCISE_SOLUTIONS']: ({ commit }, exerciseId) => {
            commit('EXERCISE_REQUEST')
            service.exercise.getExerciseUserSolutions(exerciseId)
            .then(resp => {
                commit('SET_EXERCISE_SOLUTIONS', resp.data)
                commit('EXERCISE_SUCCESS')
            })
            .catch(err => {
                commit('EXERCISE_ERROR', err)
            })
        },
    }
}