import Vue from 'vue'
import Router from 'vue-router'

import Index from '../views/Index.vue'

Vue.use(Router)

const routes = [
  {
    path: '/',
    name: 'index',
    component: Index
  },
  {
    path: '/intranet',
    name: 'intranet',
    meta: { requiresAuth: true },
    component: () => import('../views/private/Intranet.vue'), // Lazy load import
    children: [
      {
        path: 'dashboard',
        component: () => import('../views/private/Dashboard.vue')
      },
      {
        path: 'exercise',
        component: () => import('../views/private/exercise/Index.vue')
      },
      {
        path: 'exercise/explore',
        component: () => import('../views/private/exercise/Explore.vue')
      },
      {
        path: 'exercise/play',
        component: () => import('../views/private/exercise/Play.vue')
      },
      {
        path: 'challenges',
        component: () => import('../views/private/exercise/Challenges.vue')
      },
      {
        path: 'settings',
        component: () => import('../views/private/Settings.vue')
      },
      {
        path: '*',
        component: () => import('../views/public/404.vue')
      }
    ],
  },
  {
    path: '/validation',
    name: 'validation',
    component: () => import('../views/public/auth/Validation.vue')
  },
  {
    path: '/restart',
    name: 'restart',
    component: () => import('../views/public/auth/Restart.vue')
  },
  {
    path: '*',
    component: () => import('../views/public/404.vue')
  }
]

const router = new Router({
  routes
})

// Perms sections
router.beforeEach((to, from, next) => {
  // this route requires auth, check if logged in
  // if not, redirect to login page.
  if (to.matched.some(record => record.meta.requiresAuth)) {
    // router.app is the app global instance
    var loguedin = router.app.$store.getters['auth/isAuthenticated']
    if (!loguedin) {
      next({
        path: '/',
        query: { redirect: to.fullPath, error: true, msg: 'Not logued in' }
      })
    } else {
      next()
    }
  } else {
    next() // make sure to always call next()!
  }
})

export default router;
