<style scoped lang="scss">
.index {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url("../assets/img/working-at-night.jpg") no-repeat center;
  background-size: cover;

  font-family: "Montserrat", sans-serif;
  font-size: 12px;
  line-height: 1em;

  .md-progress-bar {
    width: 100%;
  }
  .logo {
    position: relative;
    top: 5vh;
    left: 5vw;
  }
  .multilang {
    position: absolute;
    top: 5vh;
    right: 1vw;
  }
  .center-container {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 90vh;
  }
}

@media screen and (max-width: 990px) {
  .logo {
    width: 80px;
  }
}
</style>

<template>
  <div class="index">
    <md-progress-bar v-if="ifLoading" class="md-accent" md-mode="indeterminate"></md-progress-bar>

    <img class="logo" src="../assets/logo/logoAsys.png" alt="Logo Asys">
    <multilang-button transparent class="multilang"/>

    <div class="center-container">
      <login-register-form/>
    </div>

    <md-snackbar
      md-position="center"
      :md-duration="snack.duration"
      :md-active.sync="ifError && snack.showSnackbar"
      md-persistent
    >
      <span>{{ this.$store.state.auth.msg }}</span>
      <md-button class="md-primary" @click="snack.showSnackbar = false">Retry</md-button>
    </md-snackbar>
  </div>
</template>

<script>
import LoginRegisterForm from "@/components/login-register-form.vue";
import MultilangButton from "@/components/multilang-button.vue";

import axios from "axios";

export default {
  name: "Index",
  components: {
    LoginRegisterForm: LoginRegisterForm,
    MultilangButton: MultilangButton
  },
  data() {
    return {
      snack: {
        showSnackbar: false,
        position: "center",
        duration: 6000
      }
    };
  },
  computed: {
    ifLoading: function() {
      // computed properties reevaluate result when vars change
      return this.$store.state.auth.status == "loading";
    },
    ifError: function() {
      // eslint-disable-next-line
      this.snack.showSnackbar = true;
      return this.$store.state.auth.status == "error";
    }
  },
  created: function() {
    const token = localStorage.getItem("user-token");
    if (token) {
      // Save Authorization header for all futur request
      axios.defaults.headers.common["Authorization"] = token;
      this.$store.dispatch("user/USER_REQUEST").then(() => {
        this.$router.push("/intranet/dashboard");
      });
    }
  }
};
</script>
