import service from '@/services/'

export default {
    namespaced: true,
    state: {
        token: localStorage.getItem('user-token') || '',
        status: '',
        msg: ''
    },
    getters: {
        isAuthenticated: state => !!state.token,
        authStatus: state => state.status,
    },
    mutations: {
        ['AUTH_REQUEST']: (state) => {
            state.status = 'loading'
        },
        ['AUTH_SUCCESS']: (state, token) => {
            state.status = 'success'
            state.token = token
        },
        ['AUTH_ERROR']: (state, msg) => {
            state.status = 'error'
            state.msg = msg.response.data.message || ''
        },
        ['AUTH_LOGOUT']: (state) => {
            state.token = ''
        }
    },
    actions: {
        ['AUTH_LOGIN']: ({ commit, dispatch }, user) => {
            return new Promise((resolve, reject) => { // The Promise used for router redirect in login
                commit('AUTH_REQUEST')
                service.auth.login(user)
                    .then(resp => {
                        const token = resp.data.token
                        localStorage.setItem('user-token', token) // store the token in localstorage
                        // Save Authorization header for all futur request
                        service.setToken(token);
                        //service.$axios.defaults.headers.common['Authorization'] = token
                        commit('AUTH_SUCCESS', token)
                        // you have your token, now log in your user :)
                        dispatch('user/USER_REQUEST', null, { root: true })
                        resolve(resp)
                    }).catch(err => {
                        commit('AUTH_ERROR', err)
                        localStorage.removeItem('user-token') // if the request fails, remove any possible user token if possible
                        reject(err)
                    })
            })
        },
        ['AUTH_REGISTER']: ({ commit }, data) => {
            return new Promise((resolve, reject) => { // The Promise used for router redirect in login
                commit('AUTH_REQUEST')
                service.auth.register(data)
                    .then(resp => {
                        commit('AUTH_SUCCESS')
                        resolve(resp)
                    }).catch(err => {
                        commit('AUTH_ERROR', err)
                        reject(err)
                    })
            })
        },
        ['AUTH_VALIDATE']: ({ commit, dispatch }, params) => {
            return new Promise((resolve, reject) => { // The Promise used for router redirect in login
                commit('AUTH_REQUEST')
                service.auth.validate(params)
                    .then(resp => {
                        const token = resp.data.token
                        localStorage.setItem('user-token', token) // store the token in localstorage
                        // Save Authorization header for all futur request
                        service.setToken(token);
                        commit('AUTH_SUCCESS', token)
                        // you have your token, now log in your user :)
                        dispatch('user/USER_REQUEST', null, { root: true })
                        resolve(resp)
                    }).catch(err => {
                        commit('AUTH_ERROR', err)
                        reject(err)
                    })
            })
        },
        ['AUTH_FORGET']: ({ commit }, params) => {
            return new Promise((resolve, reject) => { // The Promise used for router redirect in login
                commit('AUTH_REQUEST')
                service.auth.forget(params)
                    .then(resp => {
                        commit('AUTH_SUCCESS')
                        resolve(resp)
                    }).catch(err => {
                        commit('AUTH_ERROR', err)
                        reject(err)
                    })
            })
        },
        ['AUTH_RESTART']: ({ commit, dispatch }, data) => {
            return new Promise((resolve, reject) => { // The Promise used for router redirect in login
                commit('AUTH_REQUEST')
                service.auth.restart(data)
                    .then(resp => {
                        const token = resp.data.token
                        localStorage.setItem('user-token', token) // store the token in localstorage
                        // Save Authorization header for all futur request
                        service.setToken(token);
                        commit('AUTH_SUCCESS', token)
                        // you have your token, now log in your user :)
                        dispatch('user/USER_REQUEST', null, { root: true })
                        resolve(resp)
                    }).catch(err => {
                        commit('AUTH_ERROR', err)
                        reject(err)
                    })
            })
        },
        ['AUTH_LOGOUT']: ({ commit }) => {
            return new Promise((resolve) => {
                commit('AUTH_LOGOUT')
                commit('user/AUTH_LOGOUT', null, { root: true })
                localStorage.removeItem('user-token') // clear your user's token from localstorage
                localStorage.removeItem('user-profile')
                resolve()
            })
        }
    }
}