<style scoped>
.multilang-button {
  width: 10em;
}
.multilang-button select {
  background-color: transparent;
  color: #fff;
  border: none;
  outline: none;
  cursor: pointer;
}
.multilang-button select option {
  background-color: #82caff;
}
</style>


<template>
  <div class="multilang-button">
    <select v-if="transparent" v-model="selected">
      <option value="en">en</option>
      <option value="es">es</option>
    </select>

    <md-field v-else>
      <label for="language">{{ $t("settings.form_label_lang") }}</label>
      <md-select v-model="selected" name="language" id="language">
        <md-option value="en">en</md-option>
        <md-option value="es">es</md-option>
      </md-select>
    </md-field>
  </div>
</template>
<script>
export default {
  name: "multilang-button",
  props: {
    transparent: Boolean
  },
  data() {
    return {
      selected: this.$i18n.locale
    };
  },
  watch: {
    selected: function() {
      this.changeLang();
    }
  },
  methods: {
    changeLang: function() {
      this.$store.dispatch("settings/SET_LANG", { lang: this.selected });
    }
  }
};
</script>
